import { MailOutlined, BellFilled } from '@ant-design/icons';
import { Badge, Drawer, Image, List, Space, Typography } from 'antd';
import React, { useEffect, useState } from 'react';
import { GetComments, GetOrder } from '../../API/GetOrders';

const Header=() => {

    const [comments, setComments] = useState([])
    const [orders, setOrders] = useState([])

    const [commentsOpen, setCommentsOpen] = useState(false)
    const [notificationOpen, setNotificationOpen] = useState(false)

    useEffect(()=> {
        GetComments().then(res=>{
            setComments(res.comments)
        });
        GetOrder().then(res=>{
            setOrders(res.products)
        });
    })


    return(
        <div className='Header'>
            <Image width={40} src="https://w7.pngwing.com/pngs/403/269/png-transparent-react-react-native-logos-brands-in-colors-icon-thumbnail.png"></Image>
            <Typography.Title>Admin Dashboard</Typography.Title>
            <Space>
                <Badge count={10} dot>
                    <MailOutlined style={{ fontSize : 24 }} onClick={()=>{
                        setCommentsOpen(true)
                    }}/>
                </Badge>
                <Badge count={10}>
                    <BellFilled style={{ fontSize : 24 }} onClick={()=>{
                        setCommentsOpen(true)
                    }}/>
                </Badge>
            </Space>
            {/* <Space>
                {comments && (
                    <Badge count={comments.length} dot>
                    <MailOutlined
                        style={{ fontSize: 24 }}
                        onClick={() => {
                        setCommentsOpen(true);
                        }}
                    />
                    </Badge>
                )}
                {orders && (
                    <Badge count={orders.length}>
                    <BellFilled
                        style={{ fontSize: 24 }}
                        onClick={() => {
                        setNotificationOpen(true);
                        }}
                    />
                    </Badge>
                )}
            </Space> */}
            <Drawer title="Comments" open={commentsOpen} onClose={()=>{
                setCommentsOpen(false)
            }}
            maskClosable
            >
                <List dataSource={comments} renderItem={(item)=> {
                    return <List.Item>{item.body}</List.Item>
                }}></List>
            </Drawer>

            <Drawer title="Notifications" open={notificationOpen} onClose={()=>{
                setNotificationOpen(false)
            }}
            maskClosable
            >
                <List dataSource={orders} renderItem={(item)=> {
                    return <List.Item><Typography.Text strong>{item.title}</Typography.Text> has been ordered!</List.Item>
                }}></List>
            </Drawer>
        </div>
    )
}

export default Header;