import {ShoppingCartOutlined, ShoppingOutlined, UserOutlined, DollarCircleOutlined} from "@ant-design/icons";
import { Card, Space, Statistic, Typography} from "antd";
import React, { useEffect, useState } from "react";
import { GetCustomer, GetInventory, GetOrder } from "../../API/GetOrders";
// import DashboardChart from './DashboardChart' ;
import RecentOrders from "./RecentOrders";



const Dashboard = () => {

  const [orders, setOrders] = useState(0);
  const [inventory, setInventory] = useState(0);
  const [customers, setCustomers] = useState(0);
  const [revenue, setRevenue] = useState(0);

  useEffect(() => {
    GetOrder().then(res=>{
      setOrders(res.total)
      setRevenue(res.discountedTotal)
    });
    GetInventory().then(res=>{
      setInventory(res.total)
    });
    GetCustomer().then(res=>{
      setCustomers(res.total)
    })
  },[])

  return (
    <Space size={20} direction="vertical">
      <Typography.Title level={4}>Dashboard</Typography.Title>
      <Space direction="horizontal">
        <DashboardCard
          icon={
            <ShoppingCartOutlined
              style={{
                color: "green",
                backgroundColor: "rgba(0, 255, 0, 0.25)",
                borderRadius: 20,
                fontSize: 24,
                padding: 8,
              }}
            />
          }
          title={"Orders"}
          value={orders}
        />
        <DashboardCard
          icon={<ShoppingOutlined
            style={{
                color: "blue",
                backgroundColor: "rgba(0, 0, 255, 0.25)",
                borderRadius: 20,
                fontSize: 24,
                padding: 8,
              }}
            />}
          title={"Inventory"}
          value={inventory}
        />
        <DashboardCard
          icon={<UserOutlined
            style={{
                color: "purple",
                backgroundColor: "rgba(0, 255, 255, 0.25)",
                borderRadius: 20,
                fontSize: 24,
                padding: 8,
              }} 
            />}
          title={"Customers"}
          value={customers}
        />
        <DashboardCard
          icon={<DollarCircleOutlined 
            style={{
                color: "red",
                backgroundColor: "rgba(255, 0, 0, 0.25)",
                borderRadius: 20,
                fontSize: 24,
                padding: 8,
              }}
            />}
          title={"Revenue"}
          value={revenue}
        />
      </Space>
      <Space>
        <RecentOrders/>
        {/* <DashboardChart /> */}
      </Space>
    </Space>
  );
};

function DashboardCard({ title, value, icon }) {
  return (
    <Card>
      <Space direction="horizontal">
        {icon}
        <Statistic title={title} value={value}></Statistic>
      </Space>
    </Card>
  );
}


